.dashboard-wrapper {
    .header {
        margin: 40px 0;
        &.full {
            margin: 0 0 29px;
            padding: 26px 0;
            border-bottom: 1px solid #D4D4D4;
            @include max576 {
                padding: 18px 0 24px;
                margin-bottom: 20px;
            }
            .row {
                max-width: 1370px;
                width: 100%;
                padding: 0 20px;
                margin: 0 auto;
            }
            .header-logo {
                display: block;
                margin-right: 111px;
                @include max992 {
                    margin-right: 0;
                }
            }
        }
        @include max992 {
            margin: 20px 0;
        }

        .row {
            display: flex;
            align-items: center;

            @include max992 {
                margin-bottom: 20px;

                &>.search-wrapper {
                    display: none;
                }
            }
        }

        .search-wrapper {
            height: 48px;
            background: #FFFFFF;
            border-radius: 12px;
            position: relative;
            padding: 4px;
            max-width: 384px;
            flex: 0 0 33%;
            display: flex;
            align-items: center;

            @include max992 {
                max-width: 100%;
            }

            @include max576 {
                justify-content: space-between;
            }

            &>svg {
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                left: 16px;

                @include max576 {
                    left: 10px;
                }
            }

            input {
                flex: 1;
                padding-left: 53px;

                @include max576 {
                    flex: unset;
                    width: 200px;
                    padding-left: 47px;
                }
            }
        }

        &>.search-wrapper {
            display: none;

            @include max992 {
                display: flex;
            }
        }

        .select-wrapper {
            width: 118px;
            flex: 0 0 118px;
            height: 100%;
            background: #EAEDEE;
            border-radius: 10px;
            font-weight: 500;
            font-size: 14px;
            line-height: 130%;
            color: #1B2124;
            position: relative;

            @include max576 {
                width: 99px;
                flex: 0 0 99px;
                font-weight: 500;
                font-size: 12px;
                line-height: 130%;
            }

            select {
                border: none;
                padding: 0 14px;
                background: transparent;
                height: 100%;
                width: 100%;
                @include selectDefault;

                @include max576 {
                    padding: 0 10px;
                }
            }

            svg {
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                right: 14px;

                @include max576 {
                    right: 10px;
                }

            }
        }

        .notification,
        .settings,
        .user {
            width: 49px;
            height: 48px;
            flex: 0 0 48px;
            background: #FFFFFF;
            box-shadow: 0px 18px 54px rgba(0, 0, 0, 0.02);
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;
            cursor: pointer;

            @include max576 {
                width: 38px;
                height: 38px;
                flex: 0 0 38px;

                svg {
                    width: 18px;
                    height: 18px;
                }
            }
        }

        .settings {
            margin-right: 28px;

            @include max576 {
                margin-right: 14px;
            }
        }

        .user {
            @include max576 {
                img {
                    width: 31px;
                    height: 31px;
                    overflow: hidden;
                    object-fit: cover;
                }
            }
        }

        .notification {
            margin-right: 28px;
            margin-left: auto;

            @include max576 {
                margin-right: 14px;
            }

            &::after {
                content: attr(data-count);
                position: absolute;
                width: 28px;
                height: 19px;
                right: -10px;
                top: 0;
                background: #FE6E66;
                border-radius: 100px;
                font-weight: 500;
                font-size: 12px;
                text-align: center;
                line-height: 18px;
                color: #FFFFFF;

                @include max576 {
                    width: 25px;
                    font-size: 10px;
                    line-height: 15px;
                    height: 15px;
                }
            }
        }

        .header-logo {
            display: none;
            margin-bottom: 8px;

            @include max992 {
                display: block;
            }

            @include max576 {
                margin-bottom: 4px;

                img {
                    width: 98px;
                    height: auto;
                }
            }
        }

    }
}